.buttons-container {
  width: 97px;
  z-index: 999;
  position: fixed;
  display: flex;
  top: 30px;
  border-radius: 45px;
  right: 10px;
  box-shadow: inset 0 0 5px 9px rgba(0, 0, 0, 0.911);
}

.buttons-container ul {
  padding: 0px;
  width: inherit;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: inherit;
  margin: 10px auto;
  list-style: none;
}

.buttons-container li {
  vertical-align: center;
  text-align: center;
  margin: 1px;
  display: flex;
  height: 50px;
  width: 50px;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 15px;
  font-size: 40px;
  color: gray;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  background: #202020;
  box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.5);
}

.buttons-container li:nth-of-type(1) {
  border-top-left-radius: 50px;
  border-top-right-radius: 50px;
}

.buttons-container li:last-of-type {
  border-bottom-left-radius: 50px;
  border-bottom-right-radius: 50px;
}

.buttons-container li:hover {
  cursor: pointer;
}

.buttons-container li a {
  display: inline-block;
  color: gray;
  text-decoration: none;
  font-size: 40px;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}

.active-text {
  -webkit-animation: blink 3s linear infinite;
          animation: blink 3s linear infinite;
  color: rgba(255, 157, 0, 0.952) !important;
  text-shadow: 0 0 1px rgba(255, 157, 28, 0.726);
}

.fas,
.far {
  width: inherit;
}

.active {
  -webkit-animation: blink3 3s linear infinite;
          animation: blink3 3s linear infinite;
  background: #151515 !important;
  border-top: none !important;
  border-bottom: 1px solid #252525 !important;
  box-shadow: inset 0 0 10px 0px rgba(255, 157, 28, 0.726) !important;
}

@-webkit-keyframes blink {
  10% {
    color: rgba(255, 157, 0, 0.952);
