  }
  50% {
    color: rgba(255, 157, 0, 0.952);
    text-shadow: 0 0 5px rgba(255, 157, 28, 0.726);
  }
  70% {
    color: rgba(255, 157, 0, 0.952);
    text-shadow: 0 0 3px rgba(255, 157, 28, 0.726);
  }
  90% {
    color: rgba(255, 157, 0, 0.952);
    text-shadow: 0 0 1px rgba(255, 157, 28, 0.726);
  }
}

@keyframes blink {
