    box-shadow: inset 0 0 5px 2px rgba(12, 8, 4, 0.726);
  }
  30% {
    box-shadow: inset 0 0 15px 3px rgba(12, 8, 4, 0.726);
  }
  50% {
    box-shadow: inset 0 0 5px 2px rgba(255, 157, 0, 0.952);
  }
  70% {
    box-shadow: inset 0 0 15px 3px rgba(12, 8, 4, 0.726);
  }
  90% {
    box-shadow: inset 0 0 5px 2px rgba(12, 8, 4, 0.726);
